import { Controller } from '@ali/4ever-cangjie';
import type { Node } from '@ali/4ever-cangjie';
import { Table, TableCell, TableRow } from '@ali/4ever-plugin-table';

export function isColumnsParent(controller: Controller, node: Node) {
  return Table.isTable(node) && node.data.sr;
}

export function isColumnsRow(controller: Controller, node: Node) {
  const parent = controller.value.document.getParent(node.key);
  return parent && isColumnsParent(controller, parent) && TableRow.isTableRow(node);
}

export function isColumnsChild(controller: Controller, node: Node) {
  if (node && TableCell.isTableCell(node)) {
    const parent = controller.value.document.getParent(node.key);
    return parent && isColumnsRow(controller, parent);
  }
  return false;
}
